* {
  box-sizing: border-box;
  max-width: 100%;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-image: url(https://www.pngkit.com/png/detail/110-1105172_chuck-norris-chuck-norris-cartoon-pics-transparent.png);
  background-repeat: no-repeat;
  background-position: center;
}

h1 {
  text-align: center;
  font-size: 3rem;
}

button {
  width: 12%;
  margin: 1% 2%;
  border: 1px solid #000;
  border-radius: 7px;
  padding: 1% 2%;
  text-transform: capitalize;
  outline: none;
  opacity: 0.8;
}

button:hover {
  cursor: pointer;
}

.active {
  background: #b62005;
  border-color: #b62005;
  color: #fff;
}

.category-btns {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 80%;
  margin: 5% auto;
}

.new-fact {
  display: block;
  margin: 2% auto;
}

h3 {
  width: 40%;
  margin: 1% auto;
  border: 1px solid #000;
  padding: 2%;
  font-size: 1.6rem;
  text-align: center;
  background: #fff;
  opacity: 0.8;
}

p {
  width: 60%;
  margin: 20% auto 0;
  border: 1px solid #000;
  padding: 3%;
  font-size: 1.6rem;
  background: #fff;
  opacity: 0.8;
}

#root {
  max-width: 850px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.4);
  height: 100vh;
}

@media (max-width: 500px) {
  button {
    width: 21%;
  }

  p{
    margin-top: 50%;
  }
}